.album {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
}

.imgAlbum{
    border-radius: 10%;
    border: solid 1px black;
    padding: 0px;
    overflow: hidden;
    transition-property: all;
    transition-duration: 1s;
}

.navAlbum{
    display: flex;
    margin: 5px;
    gap: 10px;
}

